<script>
  let stepSize = 30;
  let moveBX = 170;
  let moveJX = 340;
  let moveBY = 140;
  let moveJY = 140;
  let moveYDown = 140;
  let moveYUp = 130;
  let moveUp = true;
  let scene = -1;
  let intermission = false;
  let bruid = true;
  let bruidegom = true;
  let jasper = true;

  const nextScene = () => {
    scene += 1;
    intermission = true;
    setTimeout(() => {
      intermission = false;
    }, 300);

    if (scene == 0) {
      moveBX = 170;
      moveJX = 340;
    } else {
      moveBX = 50;
      moveJX = 500;
    }
  };

  const previousScene = () => {
    scene -= 1;
    intermission = true;
    setTimeout(() => {
      intermission = false;
    }, 300);

    if (scene == 0) {
      moveBX = 170;
      moveJX = 340;
    } else {
      moveBX = 500;
      moveJX = 50;
    }
  };

  $: if ((scene == 1 || scene == 2) && moveBX > 550) {
    nextScene();
  }

  $: if (scene == 2 && moveBX < 0) {
    previousScene();
  }

  const selectBernadet = () => {
    bruid = true;
    bruidegom = false;
    nextScene();
  };

  const selectJelmer = () => {
    bruid = false;
    bruidegom = true;
    nextScene();
  };
  let moveLinks = () => {
    if (bruid) {
      moveBX -= stepSize;
    }

    if (bruidegom) {
      moveJX -= stepSize;
    }

    stepUp();
  };

  const stepUp = () => {
    if (bruid) {
      moveBY = moveYUp;
    }

    if (bruidegom) {
      moveJY = moveYUp;
    }

    setTimeout(() => {
      moveJY = moveYDown;
      moveBY = moveYDown;
    }, 100);
  };

  let moveRechts = () => {
    if (bruid) {
      moveBX += stepSize;
    }

    if (bruidegom) {
      moveJX += stepSize;
    }
    stepUp();
  };
  let action = () => {
    console.log("action");
  };

  let intervalHighlight;
  let timeoutHighlight;
  let disableLower = false;
  let disableHigher = false;

  function oneLower() {
    moveBX -= 1;
    if (0 === moveBX) {
      stopTimers();
    }
    stepUp();
  }
  function startIntervalLower() {
    timerBase = setTimeout(setLowerFunction, 500);
  }
  const setHigherFunction = () => {
    moveBX += 5;
    stepUp();
    if (600 <= moveBX) {
      stopTimers();
    } else {
      timerNested = setTimeout(setHigherFunction, 80);
    }
  };
  const setLowerFunction = () => {
    moveBX -= 5;
    stepUp();
    if (0 >= moveBX) {
      moveBX = 0;
      stopTimers();
    } else {
      timerNested = setTimeout(setLowerFunction, 80);
    }
  };
  let timerBase;
  let timerNested;
  function startIntervalHigher() {
    disableLower = false;
    timerBase = setTimeout(setHigherFunction, 300);
  }
  function stopTimers() {
    clearTimeout(timerBase);
    clearTimeout(timerNested);
  }
  function oneHigher() {
    disableLower = false;
    nameIndex += 1;
    // if (chartData.length - 1 === nameIndex) {
    //   stopTimers();
    //   disableHigher = true;
    // }
  }
</script>

